* {
  box-sizing: border-box;
}

html {
  font-family: Bahnschrift;
}

body {
  margin: 0;
  background-color: $clr5;
  // height: 100vh;
}

p {
  margin: 0 0 1em;
}

a {
  text-decoration: none;
  color: unset;
}


/* Dont stretch my Media */
img,
embed,
video,
object {
  max-width: 100%
}

img {
  width: 100%;
}

.red {
  color: red;
}


/* Simple and useful CSS Debugging Technique */
.debug * {
  outline: 1px solid gold
}

a.active {
  text-decoration: overline;
}



.popup-menu {
    position: fixed;
    flex-direction: column;
    padding: 40px;
    width: 40vw;
    align-items: center;
    height: auto;
    background-color: rgb(248, 250, 247);
    border: 1px solid #bebebe;
    box-shadow: 1px 2px 16px #000000a6;
    display: flex;
    inset: 0;
    margin: auto;
    height: fit-content;
}
.popup-menu h1 {
    font-size: 1.5em;
}